@import url('https://fonts.googleapis.com/css2?family=Montserrat:400,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caladea&display=swap');
@import "_PaintBX_variables";
@import "_PaintBX_titles";

* {
    box-sizing: border-box;
}

body {
    font-family: "Caladea";
    font-style: italic;
    padding: 0px 140px;
    padding-top: 70px;
}
header {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    padding: 0px 140px;
    background-color: white;
    height: 70px;
    display: flex;
    align-items: center;
    nav {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        ul {
            list-style: none;
            display: flex;
            text-transform: uppercase;
            font-size: 10px;
            &:first-child{
                li {
                    margin-right: 60px;
                    @extend %miniTitle;
                }
            }
        }
    }
}

.hero {
    height: 100vh;
    width: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
     url("https://images.unsplash.com/photo-1583248369069-9d91f1640fe6?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1352&q=80");
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    h4 {
        @extend %miniTitle;
    }

    h3 {
        font-size: 40px;
        width: 35%;
        text-align: center;
        margin-bottom: 20px;
    }
    a {
        width: 10%;
        background-color: white;
        padding: 20px 0px;
        text-align: center;
        text-decoration: none;
        color: black;
        @extend %miniTitle;
        font-size: 10px;
    }
}